.getSheet-btn-block {
  width: 250px;
  height: 150px;
  padding: 30px 10px;
  border-radius: 20px;
  box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.2);
  margin-left: 30px;
  background: white;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.icon {
  width: 50px;
  height: 50px;
  margin-top: 20px;
}
